import React, { useState, useEffect, useRef } from "react";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
  MDBBtn,
  MDBTypography,
  MDBTextArea,
  MDBIcon,
  MDBSpinner,
} from "mdb-react-ui-kit";
import {
  Box,
  Typography,
  Button,
} from "@mui/material";
import MailIcon from "@mui/icons-material/Mail";
import "./chat.css";
import axios from "../baseurl";
import { useNavigate } from "react-router-dom";

export default function App() {
  const [messages, setMessages] = useState([
    
    
  ]);
   
  const [Chats,setChats] = useState({
    chat :[],
    currentChat : ""
  })
  const [inputText, setInputText] = useState("");
  const [selectedFile, setSelectedFile] = useState(null);
  const [filePreview, setFilePreview] = useState(null);
  const [loading, setLoading] = useState(false); // For loading bot response
  const chatEndRef = useRef(null);
  const [Email, setEmail] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    const verifyUser = async () => {
      const Token = localStorage.getItem("Token");
      if (!Token) {
        navigate("/");
      } else {
        axios
          .post("/VerifyUser/" + Token, {}, { withCredentials: true })
          .then((res) => {
            if (res.data.status) {
              setEmail(res.data.username);
            } else {
              localStorage.removeItem("Token");
              navigate("/");
            }
          });
      }
    };
    verifyUser();
  }, []);
  // Auto-scroll to the bottom when new messages are added
  useEffect(() => {
    chatEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  // Handle file upload
  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file && (file.type === "application/pdf" || file.type === "text/csv")) {
      setSelectedFile(file);
      setFilePreview(file.name); // Display the file name as preview
    } else {
      alert("Please upload a PDF or CSV file.");
      setSelectedFile(null);
      setFilePreview(null);
    }
  };

  // Remove the selected file
  const handleRemoveFile = () => {
    setSelectedFile(null);
    setFilePreview(null);
  };

  // Simulate bot response with loading animation


  // Handle sending the message with file (if any)

  // Start a new chat
  const startNewChat = async () => {
    const Token = localStorage.getItem("Token");
    const result = await axios.post(
      "/chat",
      {},
      {
        withCredentials: true,
        headers: {
          Authorization: "Bearer " + Token,
        },
      }
    );
    if (result.data.status) {
        setChats({
        ...Chats,
         chat: [result.data.chat,...Chats.chat],
         currentChat:result.data.chat._id
        })
    }
  };

  useEffect(() => {
    setMessages([]);
    const GetMessage = async () => {
      const Token = localStorage.getItem("Token");
      const result = await axios.get('/chats/'+Chats.currentChat+'/messages',{
        withCredentials: true,
        headers: {
          Authorization: "Bearer " + Token,
        },
      })
      if(result.data.status){
        setMessages(result.data.messages)
      }
    }
    if(Chats.currentChat){
    GetMessage()
    }

  }, [Chats]);

 

  const sendMessage = async () => {
    const Token = localStorage.getItem("Token");
    let input = inputText;
    let file = selectedFile;
    setInputText("");
    setSelectedFile(null);
    setFilePreview(null);

    const formData = new FormData();
    formData.append('text', input);
    formData.append('sender', 'user');
    if (file) {
      formData.append("file", file);
    }
    let localMessages = [...messages,{
      sender: "user",
      text: input,
      fileUrl:{ 
        name:file ? file.name : null,
        url:file ? URL.createObjectURL(file) : null}
    }];
   
    
    setMessages([...messages, {
      sender: "user",
      text: inputText,
      fileUrl:{ 
        name:file ? file.name : null,
        url:file ? URL.createObjectURL(file) : null}
    }]);
     
    setLoading(true);
    
    const result = await axios.post(
      "/chats/"+Chats.currentChat+"/messages",
      formData,
      {
        withCredentials: true,
        headers: {
          Authorization: "Bearer " + Token,
        },
      }
    );
    if (result.data.status) {
      setMessages([...localMessages, result.data.newMessage]);
      
    }
    setLoading(false);
  }

  useEffect(() => {
    const Token = localStorage.getItem("Token");
    axios
      .get("/chats", {
        withCredentials: true,
        headers: {
          Authorization: "Bearer " + Token,
        },
      })
      .then((res) => {
        if(res.data.chats.length>0){
        setChats({
          currentChat : res.data.chats[0]._id,
          chat :res.data.chats
        });
      }
      });
      
  }, []);

  const AppHeader = ({ email, handleLogout }) => {

    const navigate = useNavigate();
  
    return (
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          padding: "9px 25px",
          background: "linear-gradient(to right,rgba(252, 203, 144, 1),rgba(213, 126, 235, 1));",
          color: "white",
          height:'7vh',
          boxShadow: "0 4px 10px rgba(0, 0, 0, 0.15)",
        }}
      >
        <Box display="flex" alignItems="center">
          <MailIcon sx={{ marginRight: 1, color: "#fff" }} />
          <Typography
            variant="h6"
            sx={{ fontWeight: "bold", fontSize: "1.5rem" }}
          >
            {email}
          </Typography>
        </Box>
        <Box>
        <Button
          color="inherit"
          onClick={() => navigate("/")}
          sx={{
            color: "white",
            fontWeight: "bold",
            textTransform: "uppercase",
            ":hover": { backgroundColor: "rgba(255, 255, 255, 0.2)" },
          }}
        >
          Home
        </Button>
       
        </Box>
        <Button
          onClick={handleLogout}
          sx={{
            color: "white",
            fontWeight: "bold",
            textTransform: "uppercase",
            ":hover": { backgroundColor: "rgba(255, 255, 255, 0.2)" },
          }}
        >
          Logout
        </Button>
      </Box>
    );
  };

  return (
    <>
    <AppHeader email={Email} handleLogout={() => navigate("/")}/>
    <MDBContainer fluid className="py-0 gradient-custom" style={{height:"93vh"}}>
      
      <MDBRow style={{  }}>
        <MDBCol md="6" lg="5" xl="4">
          <MDBCard
            className="mask-custom"
            style={{ padding: 0, marginTop: "2vh" }}
          >
            <MDBCardBody>
              {/* Chat History and New Chat Icon aligned to the right */}
              <div className="d-flex justify-content-between align-items-center mb-3">
                <h5 className="text-white mb-0">Chat History</h5>
                <MDBIcon
                  fas
                  icon="pen" // Replaced 'plus-circle' with 'pen' icon
                  size="lg"
                  className="text-white"
                  onClick={startNewChat}
                  style={{ cursor: "pointer" }}
                />
              </div>

              <MDBTypography
                style={{ height: "78vh", overflow: "scroll" }}
                listUnStyled
                className="mb-0"
              >
                {Chats.chat.map((chat, index) => (
                  <li className="p-2 border-bottom" style={Chats.currentChat===chat._id ? {backgroundColor:"rgba(255,255,255,0.1)"} : {} }>
                  <a
                    href="#!"
                    onClick={() => setChats({...Chats,currentChat:chat._id})}
                    className="d-flex justify-content-between link-light"
                    
                  >
                    <div className="d-flex flex-row">
                      <div className="pt-1">
                        <p className="fw-bold mb-0">{chat.name}</p>
                      </div>
                    </div>
                  </a>
                </li>
                ))}
                {/* Chat history would be displayed here (for now, it's just a placeholder) */}
                
                {/* Add more chats here */}
              </MDBTypography>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>

       {Chats.currentChat!=="" && <MDBCol md="6" lg="7" xl="8">
          <MDBTypography
            listUnStyled
            className="text-white"
            style={{ marginTop: "2vh" }}
          >
            <div
              style={{
                backgroundColor: "transparent",
                height: filePreview ? "63vh" : "73vh", // Fixed height for the chat area
                overflowY: "scroll", // Only the chat messages will scroll
                overflowX: "hidden",
              }}
            >
              {/* Chat messages */}
              {messages.map((message, index) =>
                message.sender === "bot" ? (
                  <li
                    className="d-flex mb-4"
                    style={{
                      width: "70%",
                      justifyContent: "flex-start",
                      alignItems: "flex-start",
                      alignSelf: "flex-start",
                      textAlign: "start",
                    }}
                    key={index}
                  >
                    <MDBCard className="w-100 mask-custom">
                      <MDBCardBody>
                        <p className="mb-0">{message.text}</p>
                      </MDBCardBody>
                    </MDBCard>
                  </li>
                ) : (
                  <li
                    className="d-flex mb-4"
                    style={{
                      width: "70%",
                      justifyContent: "flex-end",
                      marginLeft: "auto",
                    }}
                    key={index}
                  >
                    <MDBCard className="w-100 mask-custom">
                      <MDBCardBody>
                        <p className="mb-0">{message.text}</p>
                        {message.fileUrl.name && (
                          <p style={{ marginTop: "10px", fontStyle: "italic" }}>
                            Uploaded File: {message.fileUrl.name}
                          </p>
                        )}
                      </MDBCardBody>
                    </MDBCard>
                  </li>
                )
              )}

              {/* Loading Animation for Bot Response */}
              {loading && (
                <li
                  className="d-flex mb-4"
                  style={{
                    width: "70%",
                    justifyContent: "flex-start",
                    alignItems: "flex-start",
                    alignSelf: "flex-start",
                    textAlign: "start",
                  }}
                >
                  <MDBCard className="w-100 mask-custom">
                    <MDBCardBody className="d-flex align-items-center">
                      <MDBSpinner color="light" role="status" className="me-2">
                        <span className="visually-hidden">Loading...</span>
                      </MDBSpinner>
                      <p className="mb-0">Bot is typing...</p>
                    </MDBCardBody>
                  </MDBCard>
                </li>
              )}

              {/* Reference to scroll to the bottom */}
              <div ref={chatEndRef} />
            </div>

            {/* Advanced File Preview */}
            {filePreview && (
              <div
                className="mb-3"
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "space-between",
                  backgroundColor: "#f5f5f5",
                  padding: "10px",
                  borderRadius: "8px",
                  marginTop: "10px",
                }}
              >
                <div style={{ display: "flex", alignItems: "center" }}>
                  {/* File Icon based on the file type */}
                  {selectedFile && selectedFile.type === "application/pdf" && (
                    <MDBIcon
                      fas
                      icon="file-pdf"
                      size="2x"
                      className="me-3 text-danger"
                    />
                  )}
                  {selectedFile && selectedFile.type === "text/csv" && (
                    <MDBIcon
                      fas
                      icon="file-csv"
                      size="2x"
                      className="me-3 text-primary"
                    />
                  )}

                  {/* Display file name */}
                  <div>
                    <p className="mb-0" style={{ color: "black" }}>
                      <strong>Selected File:</strong> {filePreview}
                    </p>
                  </div>
                </div>

                {/* Remove button */}
                <MDBBtn color="danger" size="sm" onClick={handleRemoveFile}>
                  Remove
                </MDBBtn>
              </div>
            )}

            {/* Input and buttons */}
            <div
              style={{
                height: filePreview ? "10vh" : "",
                position: "relative",
                bottom: 0,
              }}
            >
              <li className="mb-3">
                <MDBTextArea
                  label="Message"
                  id="textAreaExample"
                  rows={2}
                  value={inputText}
                  onChange={(e) => setInputText(e.target.value)}
                />
              </li>

              <li className="d-flex justify-content-between mb-3">
                <div>
                  {/* Upload button */}
                  <input
                    type="file"
                    id="fileInput"
                    style={{ display: "none" }}
                    onChange={handleFileChange}
                    accept=".pdf, .csv"
                  />
                  <MDBBtn
                    color="light"
                    size="lg"
                    rounded
                    onClick={() => document.getElementById("fileInput").click()}
                  >
                    Upload
                  </MDBBtn>
                </div>

                <MDBBtn
                  color="light"
                  size="lg"
                  rounded
                  className="float-end"
                  onClick={sendMessage}
                >
                  Send
                </MDBBtn>
              </li>
            </div>
          </MDBTypography>
        </MDBCol>}
      </MDBRow>
    </MDBContainer>
    </>
  );
}
