const QuizComponent = () => {
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [selectedAnswer, setSelectedAnswer] = useState("");
  const [activeSection, setActiveSection] = useState(null);
  const [completedSections, setCompletedSections] = useState([]);
  const [aptitudeAnswers, setAptitudeAnswers] = useState({});
  const [personalityAnswers, setPersonalityAnswers] = useState({});
  const [growthAnswers, setGrowthAnswers] = useState({});
  const [showResultModal, setShowResultModal] = useState(false);
  const [showAlreadyCompletedModal, setShowAlreadyCompletedModal] = useState(false);
  const [sectionResult, setSectionResult] = useState(null);
  const [isAnimating, setIsAnimating] = useState(false);
  const [Email, setEmail] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    const verifyUser = async () => {
      const Token = localStorage.getItem("Token");
      if (!Token) {
        navigate("/");
      } else {
        axios
          .post("/VerifyUser/" + Token, {}, { withCredentials: true })
          .then((res) => {
            if (res.data.status) {
              setEmail(res.data.username);
            } else {
              localStorage.removeItem("Token");
              navigate("/");
            }
          });
      }
    };
    verifyUser();
  }, []);

  useEffect(() => {
    const Token = localStorage.getItem("Token");
    if(activeSection===null){
        axios.get("/getResults", {
          withCredentials: true,
          headers: {
            Authorization: `Bearer ${Token}`,
          },
        }).then((res) => {
            if (res.data.status) {
              let localCompletedSections = [];
              const completedSections = res.data.completedSections;
              if (completedSections.length > 0) {
                    if(completedSections.includes('aptitude')){
                        localCompletedSections.push(0)
                    }
                    if(completedSections.includes('personality')){
                        localCompletedSections.push(1)
                    }
                    if(completedSections.includes('growth')){
                      localCompletedSections.push(2)
                    }
              }
                
              setCompletedSections(localCompletedSections);
            }
          }
        );
    }
  }, [activeSection]);

  const handleAnswerChange = (event) => {
    setSelectedAnswer(event.target.value);
  };

  const handleSectionSelect = (sectionIndex) => {
    // Check if the section is already completed
    if (completedSections.includes(sectionIndex)) {
      setShowAlreadyCompletedModal(true);
    } else {
      setActiveSection(sectionIndex);
      setCurrentQuestionIndex(0); // Reset the question index to 0 when starting a new section
      setSelectedAnswer(""); // Reset the selected answer
    }
  };

  const handleNextQuestion = () => {
    if (selectedAnswer === "") return; // Don't proceed if no answer is selected
  
    // Get the local question index for the active section
    const localQuestionIndex = currentQuestionIndex + 1; // 1-based index
  
    // Update the state based on the active section (aptitude, personality, or growth)
    if (activeSection === 0) {
      setAptitudeAnswers((prev) => ({
        ...prev,
        [localQuestionIndex]: selectedAnswer,  // Store answer with correct index
      }));
    } else if (activeSection === 1) {
      setPersonalityAnswers((prev) => ({
        ...prev,
        [localQuestionIndex]: selectedAnswer,  // Store answer with correct index
      }));
    } else {
      setGrowthAnswers((prev) => ({
        ...prev,
        [localQuestionIndex]: selectedAnswer,  // Store answer with correct index
      }));
    }
  
    // Check if it's the last question, then show the result
    if (currentQuestionIndex === allQuestions.length - 1) {
      showSectionResult();
    } else {
      // Move to the next question with an animation
      setIsAnimating(true);
      setTimeout(() => {
        setCurrentQuestionIndex((prevIndex) => prevIndex + 1);
        setIsAnimating(false);
        setSelectedAnswer(""); // Clear the selected answer for the next question
      }, 300);
    }
  };

  const showSectionResult = async () => {
    const Token = localStorage.getItem("Token");
    let sectionAnswers;
    const sectionStartIndex = sections
    .slice(0, activeSection)
    .reduce((acc, section) => acc + section.questions.length, 0);
  const localQuestionIndex = currentQuestionIndex + 1;

    let Localanswers = {[localQuestionIndex]: selectedAnswer}

    let sectionKey; // This will be used to send the correct section key to the backend

  // Determine which section is being submitted
  if (activeSection === 0) {
    Localanswers={...aptitudeAnswers,...Localanswers};

    sectionAnswers = Localanswers;
    sectionKey = "aptitudeAnswers"; // Corresponds to the key expected by the backend
  } else if (activeSection === 1) {
    Localanswers={...personalityAnswers,...Localanswers};
    sectionAnswers = Localanswers;
    sectionKey = "personalityAnswers"; // Corresponds to the key expected by the backend
  } else {
    Localanswers={...growthAnswers,...Localanswers};
    sectionAnswers = Localanswers;
    sectionKey = "growthAnswers"; // Corresponds to the key expected by the backend
  }

    try {
      const response = await axios.post(
        "/submit",
        { [sectionKey]: sectionAnswers },
        {
          withCredentials: true,
          headers: {
            Authorization: `Bearer ${Token}`,
          },
        }
      );

      setSectionResult({
        section: sections[activeSection].title,
        result: response.data[`${sectionKey.replace("Answers", "Result")}`],
      });

      setCompletedSections((prev) => [...prev, activeSection]);
      setShowResultModal(true);
    } catch (error) {
      console.error(error);
    }
  };

  const allQuestions = activeSection !== null ? sections[activeSection].questions : [];

  const currentQuestion = allQuestions[currentQuestionIndex];

  return (
    <>
      <AppHeader email={Email} handleLogout={() => navigate("/")}  activeSection={activeSection} setActiveSection={setActiveSection}/>

      <Box
        sx={{
          background: "linear-gradient(90deg, #764ba2 0%, #667eea 100%)",
          height: "93vh",
          padding: "20px",
          textAlign: "center",
        }}
      >
        {activeSection === null ? (
          <>
            <Typography variant="h4" sx={{ color: "white", marginBottom: "20px" }}>
              Select a section to begin
            </Typography>
            <Box sx={{ display: "flex", flexDirection: "column", alignItems: "center", gap: "20px" }}>
              {sections.map((section, index) => (
                <Button
                  key={index}
                  variant="contained"
                  color={completedSections.includes(index) ? "success" : "primary"}
                  onClick={() => handleSectionSelect(index)}
                  sx={{
                    padding: "20px",
                    fontSize: "1.2rem",
                    width: "250px",
                  }}
                >
                  {section.title}
                </Button>
              ))}
            </Box>
          </>
        ) : (
          <>
            <Typography variant="h4" sx={{ color: "white", marginBottom: "20px" }}>
              {sections[activeSection].title}
            </Typography>

            {currentQuestion && (
              <>
                <LinearProgress
                  variant="determinate"
                  value={(currentQuestionIndex / allQuestions.length) * 100}
                  sx={{ marginBottom: "20px" }}
                />

                <Container maxWidth="sm">
                  <Fade in={!isAnimating}>
                    <Card
                      elevation={12}
                      sx={{
                        backgroundColor: "#fdfdfd",
                        padding: "30px",
                        borderRadius: "15px",
                        boxShadow: "0 10px 40px rgba(0, 0, 0, 0.15)",
                        marginBottom: "25px",
                      }}
                    >
                      <CardContent>
                        <Typography
                          variant="h6"
                          sx={{
                            fontSize: "1rem",
                            fontWeight: "bold",
                            color: "#5146d6",
                            marginBottom: "15px",
                          }}
                        >
                          Question {currentQuestionIndex + 1} of {allQuestions.length}
                        </Typography>

                        <Typography
                          variant="h5"
                          sx={{
                            fontWeight: "bold",
                            marginBottom: "25px",
                            fontSize: "1.3rem",
                          }}
                        >
                          {currentQuestion.question}
                        </Typography>

                        <FormControl component="fieldset">
                          <RadioGroup
                            value={selectedAnswer}
                            onChange={handleAnswerChange}
                          >
                            {currentQuestion.options.map((option, idx) => (
                              <FormControlLabel
                                key={option}
                                value={String.fromCharCode(97 + idx)} // a, b, c, d
                                control={<Radio sx={{ color: "#5146d6" }} />}
                                label={option}
                                sx={{ fontSize: "1.2rem", color: "#555" }}
                              />
                            ))}
                          </RadioGroup>
                        </FormControl>

                        <Box mt={3} textAlign="center">
                          <Button
                            variant="contained"
                            size="large"
                            onClick={
                              currentQuestionIndex === allQuestions.length - 1
                                ? showSectionResult
                                : handleNextQuestion
                            }
                            disabled={!selectedAnswer}
                            sx={{
                              backgroundColor: "#FF6F61",
                              color: "white",
                              fontSize: "1.2rem",
                              padding: "12px 25px",
                              borderRadius: "30px",
                              transition: "background-color 0.3s ease",
                              ":hover": { backgroundColor: "#FF5A45" },
                            }}
                          >
                            {currentQuestionIndex === allQuestions.length - 1
                              ? "Submit"
                              : "Next"}
                          </Button>
                        </Box>
                      </CardContent>
                    </Card>
                  </Fade>
                </Container>
              </>
            )}
          </>
        )}
      </Box>

      {/* Result Modal */}
      <Modal open={showResultModal} onClose={() => setShowResultModal(false)}>
        <Box
          sx={{
            width: "450px",
            padding: "50px",
            backgroundColor: "white",
            borderRadius: "15px",
            textAlign: "center",
            margin: "120px auto",
            boxShadow: "0 10px 40px rgba(0, 0, 0, 0.25)",
          }}
        >
          <CheckCircleIcon sx={{ fontSize: 80, color: "#76C7C0", mb: 2 }} />
          <Typography
            variant="h4"
            sx={{ fontSize: "2.5rem", marginBottom: "20px" }}
          >
            {sectionResult ? `Results for ${sectionResult.section}` : "Results"}
          </Typography>
          <Typography
            variant="h6"
            sx={{ fontSize: "1.8rem", marginBottom: "20px" }}
          >
            Score: {sectionResult?.result || 0}
          </Typography>
          <Button
            variant="contained"
            onClick={() => {
              setActiveSection(null);
              setShowResultModal(false)
            
            }}
            sx={{
              backgroundColor: "#5146d6",
              padding: "12px 30px",
              borderRadius: "30px",
              ":hover": { backgroundColor: "#3d34b2" },
            }}
          >
            Close
          </Button>
        </Box>
      </Modal>

      {/* Already Completed Modal */}
      <Modal
        open={showAlreadyCompletedModal}
        onClose={() => setShowAlreadyCompletedModal(false)}
      >
        <Box
          sx={{
            width: "450px",
            padding: "50px",
            backgroundColor: "white",
            borderRadius: "15px",
            textAlign: "center",
            margin: "120px auto",
            boxShadow: "0 10px 40px rgba(0, 0, 0, 0.25)",
          }}
        >
          <CheckCircleIcon sx={{ fontSize: 80, color: "#FF6F61", mb: 2 }} />
          <Typography variant="h5" sx={{ marginBottom: "20px" }}>
            You have already completed this section!
          </Typography>
          <Button
            variant="contained"
            onClick={() => setShowAlreadyCompletedModal(false)}
            sx={{
              backgroundColor: "#5146d6",
              padding: "12px 30px",
              borderRadius: "30px",
              ":hover": { backgroundColor: "#3d34b2" },
            }}
          >
            OK
          </Button>
        </Box>
      </Modal>
    </>
  );
};

export default QuizComponent;